/* ------ */
/* --- elements style --- */
html {
  height: 100%;
  font-size: 16px; }

body {
  font-style: normal;
  font-weight: 400;
  background-color: #ffffff;
  font-size: 1rem;
  position: relative;
  min-height: 100%;
  color: #3C3C3C;
  font-family: "Open Sans", arial, sans-serif; }

.main-container {
  width: 100%;
  min-height: 100vh; }

.lr-inner-container {
  width: calc(100% - 300px);
  padding: 25px 35px 50px; }

.lr-container {
  width: 74%;
  margin-top: 50px;
  margin-bottom: 50px;
  margin-left: auto;
  margin-right: auto; }

button {
  cursor: pointer; }

pre,
.example {
  margin: 0 0 15px 0;
  padding-top: 25px;
  padding-bottom: 25px;
  padding-left: 30px;
  padding-right: 25px;
  border-radius: 5px;
  line-height: 1.4; }
  pre p,
  .example p {
    margin: 0; }

.text__red {
  color: red; }

.icon {
  width: 50px;
  height: 50px; }
  .icon.icon-close {
    fill: red;
    width: 100px;
    height: 100px; }
  .icon.icon-mark {
    fill: green;
    width: 30px;
    height: 30px; }
  .icon.icon-message {
    fill: blue;
    width: 200px;
    height: 200px; }

a {
  text-decoration: none; }

.lr-right-link__wrap {
  width: 50px; }

.lr-right-link {
  position: fixed;
  top: 0;
  right: 0;
  display: block;
  width: 50px;
  height: 100vh;
  padding: 30px 9px;
  transition: all .3s; }
  .lr-right-link svg {
    fill: #fff;
    transition: all .3s; }
  .lr-right-link:hover {
    background-color: #F0F0F0; }
    .lr-right-link:hover svg {
      fill: #000; }

ul {
  margin-top: 0;
  list-style: none; }

h1, h2, h3, h4, h5, h6 {
  margin-top: 0;
  font-weight: 700;
  line-height: 1.1; }

p {
  margin-top: 0; }

.lr-inner-container p {
  padding-left: 20px;
  padding-right: 20px; }

h2 {
  font-size: 2rem;
  margin-bottom: 1.5rem; }

h3 {
  font-size: 1.25rem;
  margin-bottom: 1.35rem; }

h4 {
  font-size: 1rem; }

.lr-create-date {
  text-align: right;
  font-style: italic;
  color: #59687c;
  font-size: .9rem;
  margin-top: 3rem;
  margin-bottom: 0; }

/* ------ */
/* --- components style --- */
.articles-list {
  width: 75%;
  max-width: 1170px;
  margin: 0 auto; }

.articles-block {
  padding: 1.8rem 1rem 1rem 2rem; }

.articles-block__header {
  display: flex;
  justify-content: flex-start;
  font-size: .95rem;
  font-weight: 700; }

.articles-block__author {
  text-decoration: none;
  color: #59687c; }

.articles-block__title {
  color: #8089ff;
  font-size: 2rem;
  font-family: "Source Sans Pro", arial, sans-serif;
  margin-bottom: .35rem; }

.articles-block__date {
  color: #a6b0bf; }

.articles-block__announce {
  color: #59687c; }

.articles-block__read {
  display: inline-block;
  color: #ffffff;
  text-align: center;
  line-height: 1.4;
  touch-action: auto;
  background: linear-gradient(45deg, #8089ff 0%, #54ceff 100%);
  font-size: .95rem;
  padding: .6rem 1.5rem;
  box-shadow: 0 7px 15px rgba(0, 9, 128, 0.05), 0 12px 28px rgba(0, 9, 128, 0.075), inset 0 1px 1px rgba(255, 255, 255, 0.1), inset 0 -1px 1px rgba(0, 0, 0, 0.075);
  cursor: pointer; }

/* ------ */
@media only screen and (max-width: 1024px) {
  html {
    font-size: 14px; }
  .lr-left-sidebar {
    display: none; }
  .lr-inner-container {
    width: calc(100% - 50px);
    padding: 20px 25px 35px; }
    .lr-inner-container p {
      padding-left: 14px;
      padding-right: 14px; }
  pre,
  .example {
    margin: 0 0 12px 0;
    padding-top: 15px;
    padding-bottom: 15px;
    padding-left: 20px;
    padding-right: 20px; } }

@media only screen and (max-width: 768px) {
  .lr-inner-container {
    width: 100%;
    padding: 20px 15px 25px; }
  .lr-right-link__wrap,
  .lr-right-link {
    position: static;
    width: 100%;
    height: 50px;
    padding: 10px; } }
